:root {
    --padding: 12px;
    --radius: 6px;
    /* --color: #333; */
    --color-light: white;
    --primary-color: #3498db;
    --primary-color-light: #add8e6;
    --bg-color-light: #DDD;

    --app-width: 100%;
    /* --app-height: 450px; */
    --left-menu-width: 300px;

    --message-min-width: 75px;
    --message-max-width: calc((var(--app-width) - var(--left-menu-width)) / 1.5);
    --message-line-height: 20px;
}

* {
    /* boont-family: 'Nunito', sans-serif; */
    /* font-weight: 600; */
    /* ox-sizing: border-box; */
    color: var(--color);
    /* futline: none; */
}

.app {
    background-color: var(--bg-color-light);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    border-radius: var(--radius);
    overflow: auto !important;
    display: flex;
    flex-direction: row;
    float: right;
    width: calc(100% - 265px);
    height: 88vh;
    margin-top: 60px;
}

.contactList {
    background-color: var(--color-light);
    width: var(--left-menu-width);
    overflow: auto;
    padding: 0px 10px;
}

.title {
    padding: 8px;
    margin: 0;
    padding-bottom: 0;
    font-size: 150%;
}

ul .users {
    list-style: none;
    margin: 0;
    padding: var(--padding);
}

.users li {
    margin: calc(var(--padding) / 1.5) 0;
    padding: 10px 5px 10px 5px;
    position: relative;
    cursor: pointer;
    transition: all .2s ease-out;
}

.users li.active,
.users li:hover {
    background-color: var(--primary-color-light);
    color: var(--primary-color);
    border-radius: var(--radius);
}

.messages {
    margin: var(--padding);
    background-color: var(--color-light);
    border-radius: var(--radius);
    overflow: auto !important;
    /* flex: 1 1 auto; */
    /* display: flex; */
    flex-direction: column;
    position: relative;
    width: 100%;
    overflow-y: scroll !important;
}

.messagesHistory {
    /* overflow-y: scroll !important; */
    /* flex: 1 1 auto; */
    /* display: flex; */
    /* flex-direction: column-reverse; */
    align-items: end;
    height: auto;
    min-height: 75vh;
    padding-top: 10px;
    /* margin-bottom: 59px; */
    /* overflow: hidden; */
}
.me{
    border: 1px solid;
}
.message {
    width: 100%;
    padding: var(--padding);
    padding-top: 0;
}

/* .message:last-child {
    padding-top: var(--padding);
} */

.message.me {
    text-align: right;
}


.message.me .messageBody {
    color: var(--color-light);
    background-color: var(--primary-color);
}

.messageBody {
    background-color: var(--bg-color-light);
    color: var(--color);
    font-weight: 400;
    display: inline-block;
    padding: 10px 15px;
    border-radius: 11px;
    line-height: var(--message-line-height);
    text-align: initial;
}

/* .messagesInputs {
    display: flex;
    position: fixed;
    bottom: 12px;
    width: 70vw !important;
} */

input[type="text"] {
    /* background-color: transparent; */
    /* border: none; */
    padding: var(--padding);
    /* resize: vertical; */
    flex: 1 1 auto;
}

button.sndMsg {
    cursor: pointer;
    padding: 10px 30px;
    text-transform: capitalize;
    background-color: #1b65a1;
    color: #fff;
    float: right;
}

.sndMsg:hover i {
    color: #fff;
}

button.sndMsg i {
    vertical-align: middle;
    transition: all .2s ease-out;
}

