.rep-details {
    background-color: #F9F9F9 !important;
    height: 900px !important;
}

table {
    border-collapse: collapse;
    width: 100%;
}

th,
td {
    text-align: left;
    padding: 8px;
}

tr:nth-child(odd) {
    background-color: #fff;
}

tr:nth-child(even) {
    background-color: #f1f5fd;
}

#icon {
    position: relative;
    right: 42px;
    top: 16px;
    width: 24px;
    height: 24px;
    color: #D0D5DD;
}