.formContent {
    margin: auto;
}

.formContent input {
    width: 100%;
    margin-right: 0px;
    padding: 10px 15px;
    border: 1px solid #55555557;
    border-radius: 10px;
    margin-bottom: 12px;
}

.formContent label {
    display: flex;
    padding-bottom: 10px;
}

.formContent h3 {
    font-size: 26px;
    font-family: poppins, sans-serif;
    margin: 20px 0px;
    text-shadow: 0 0 black;
}

.submitBtn {
    background-color: rgb(27 101 161);
    padding: 10px 35px;
    border-radius: 8px;
    font-size: 17px;
    font-family: poppins, sans-serif;
    text-shadow: 0 0 black;
    color: #fff;
    margin-top: 20px;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    appearance: none;
}

/* @media screen and (max-width: 767px) {} */

@media screen and (max-width: 640px) {
    .submitBtn {
        width: 100%;
    }
}

