.NotFoundContainer {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  
  .ContainerImg {
    width: 225px;
  }
  
  .containerHeading {
    text-align: center;
    font-weight: bold;
    font-size: 32px;
  }
  
  .containerHeading h1 {
    padding: 0;
    margin: 0;
  }
  
  
  .containerPara {
    cursor: pointer;
  }
  
  .containerPara:hover {
    text-decoration: underline;
  }
  
  @media screen and (max-width: 640px) {
    .ContainerImg {
      width: 175px;
    }
  
    .container_heacontainerHeadingding {
      font-size: 16px;
    }
  
    .containerPara {
      font-size: 12px;
    }
  
  }