.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal {
    position: relative;
    padding: 20px;
    border-radius: 4px;
    max-width: 90%;
    max-height: 90%;
    overflow: auto;
}

.closeButton {
    position: absolute;
    top: 0;
    right: 5px;
    background: none;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
}